// main
html {
  font-size: 16px;
}

body {
  font-family: 'Open Sans', sans-serif;
  color: $black;
}

* {
  box-sizing: border-box;
}

// smartgrid
.container {
  @include wrapper();
  box-sizing: content-box;
}

.container-fluid {
  @include wrapper-full();
}

.row {
  @include row-flex();
}

.col {
  @include col();
}

// text
p, strong, i {
  font-size: 1.125rem;
}

a {
  font-size: 1.375rem;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: $black;
  display: block;

  @include sm(font-size, 1.125rem);

  span {
    text-decoration: underline;
  }

  &:hover {
    span {
      text-decoration: none;
    }
  }
}

strong {
  font-weight: $bold;
}

.big {
  font-size: 1.375rem;
}

small {
  font-size: 1rem;

  a {
    font-size: 1rem;
    color: $blue-text;
    display: inline;
  }
}

::placeholder {
  font-size: 1.125rem;
  color: $grey;
}

// title
h1 {
  font: $semi-bold 3.375rem $montserrat;
  @include sm(font-size, 2rem);

  margin: 0 0 30px;
}

h2 {
  font: $semi-bold 2.265rem $montserrat;
  @include sm(font-size, 1.5rem);
  @include sm(font-weight, $bold);

  margin-bottom: 30px;
}

h3 {
  font: $semi-bold 2rem $montserrat;
  @include sm(font-size, 1.125rem);
  @include sm(font-weight, $bold);
}

h4 {
  font: $bold 1.5rem $montserrat;
  @include sm(font-size, 1.125rem);
}

h5 {
  font: $medium 1.5rem $montserrat;
  @include sm(font-size, 1.12rem);
}

// icons
.icon {
  display: flex;
  align-items: center;
}

svg {
  width: 100%;
  height: auto;
}

// section
.section {
  padding: 60px 0;

  @include sm(padding, 30px 0);
}